<template>
    <div id="recruit" style="margin-top:20px;border:5px solid gray;width:80vw;margin-left:10vw;margin-bottom:60px">
    
        <img src="../../static/本硕招生/show.jpg" style="width:80vw;height:30vh;border:1px solid gray"></img>
        <div v-html="recruitInfo" align='left' style="font-size:20px;margin:120px"></div>
  </div>
</template>

<script>

import recruit from  '../../static/本硕招生/recruit'
  export default {
    name:'recruit',
    data(){
        return {
            recruitInfo:recruit.recruitInfo,
        }
    }
  }
</script>


<style>
  *{
      margin:0px;
      padding:0px;
  }
  .block{
    width:400px;
    
    margin-bottom:50px;
    margin-left:40vw;
    transform:translateX(-50%);
  }
</style>


