<template>
    <div id="paper" style="margin-top:20px;border:5px solid gray;width:80vw;margin-left:10vw;margin-bottom:60px">
        <img src="../../static/学术论文/show.jpg" style="width:80vw;height:30vh;border:1px solid gray"></img>
        <div v-for="item,inx in paperInfo">
            <div style="font-size:22px;margin-left:1vw;color:#f15a23" align="left">{{item.year}}</div>
            <div v-for="i,inx in item['paper']">
            <div align="left" style="margin:10px;margin-left:2vw;font-size:19px;font-family:'微软雅黑'">
                <i class="el-icon-circle-check" style="color:#04BFEA"></i> {{inx+1}}. 
                <span v-html='i'></span>
            </div>
                
            </div>
        </div>
  </div>
</template>

<script>

import paper from  '../../static/学术论文/paper'
  export default {
    name:'paper',
    data(){
        return {
            paperInfo:paper.paperInfo,
            tmpCnt:0,
        }
    },
    methods:{
        yearSort:function(a,b){
            return a.year-b.year;
        }
    }
  }
</script>


<style>
  *{
      margin:0px;
      padding:0px;
  }
  .block{
    width:400px;
    
    margin-bottom:50px;
    margin-left:40vw;
    transform:translateX(-50%);
  }
</style>


