<template>
    <div id="mainPage" >
      <el-carousel height="calc(100vh - 111px)">
      <el-carousel-item v-for="item in imgCnt" :key="item.id">   
        <img :src=imgPath[item-1] style="margin-top:calc((100vh - 111px)/2);transform:translateY(-50%);height:80vh"></img>
      </el-carousel-item>
    </el-carousel>
    
  </div>
</template>

<script>
import mainPageImageConfig from '../../static/首页展示/mainPageImageConfig'

var fs = require('fs');
var path = require('path');
  export default {
    name:'mainPage',
    components:{
 
    },
    data(){
      return {
        constPath:'../../static/首页展示/',
        showImg:mainPageImageConfig.fileName,
      }
    },
    computed:{
      imgCnt:function(){
        return Object.keys(this.showImg).length;
      },

      imgPath:function(){
        let newPath=[];
         for(let i=0;i < this.showImg.length;i++){
          newPath.push(this.constPath + this.showImg[i]);
        }
        return newPath;
      }
    },
    created:function(){
    },
    methods:{
      
      func:function(){
        console.log(this.showImg);
      }
    }
  }
</script>



<style>
  *{
      margin:0px;
      padding:0px;
      
  }
  #mainPage{
    
  }
</style>


