<template>
    <div id="academicTeam" style="margin-top:20px;border:5px solid gray;width:80vw;margin-left:10vw;margin-bottom:60px;">
        <img src="../../static/学术团队/show.jpg" style="width:80vw;height:30vh;border:1px solid gray"></img>
        <div v-if='newTeacherInfo != ""'>
            <h1 style="margin-top:30px;margin-left:50px;margin-bottom:20px;font-size:30px;color:#F15A23" align="left">Faculty</h1>
            <div id="teacher">
                <div  v-for="item in newTeacherInfo" >

                    <div class="teacherItem" >
                        <div v-if="item[3].slice(-1) == '/'" :title="item[4]">
                            <img :src=nonePhoto width="114" height='156' style="border-radius:10px;box-shadow:2px 2px 5px #333333;"></img>
                        </div>
                        <div v-else>
                            <img :src=item[3] width="114" height='156' style="border-radius:10px;box-shadow:2px 2px 5px #333333;"></img>
                        </div>

                        <div v-if='item[4] == ""'>
                            <p style="color:black;font-weight:bold;font-size:20px;">{{item[0]}}</p></a>
                        </div>
                        <div v-else>
                            <a :href=item[4]  target="_blank" style="text-decoration:none;color:black;font-weight:bold;"><p style="font-size:20px;">{{item[0]}}</p></a>
                        </div>
                        <p style="font-size:15px;">{{item[1]}}</p>
                        <p style="font-size:15px;">{{item[2]}}</p>
                    </div>

                </div>
            </div>
        </div>

        <div style="margin:10px;"><hr/></div>


        <div v-if='newGraduateInfo != ""'>
            <h1 style="margin-top:30px;margin-left:50px;margin-bottom:20px;font-size:30px;color:#F15A23" align="left">Graduate Students</h1>
            <div id="teacher">
                <div  v-for="item in newGraduateInfo" >

                    <div class="teacherItem" :title="item[4]">
                        <div v-if="item[3].slice(-1) == '/'">
                            <img :src=nonePhoto width="114" height='156' style="border-radius:10px;box-shadow:2px 2px 5px #333333;"></img>
                        </div>
                        <div v-else>
                            <img :src=item[3] width="114" height='156' style="border-radius:10px;box-shadow:2px 2px 5px #333333;"></img>
                        </div>

                        <p style="color:black;font-weight:bold;font-size:20px;">{{item[0]}}</p></a>
                        <p style="font-size:15px;">{{item[1]}}</p>
                        <p style="font-size:15px;">{{item[2]}}</p>
                    </div>

                </div>
            </div>
        </div>

        <div style="margin:10px;"><hr/></div>


        <div v-if='newBachelorInfo != ""'>
            <h1 style="margin-top:30px;margin-left:50px;margin-bottom:20px;font-size:30px;color:#F15A23" align="left">Undergraduate Students</h1>
            <div id="teacher">
                <div  v-for="item in newBachelorInfo" >

                    <div class="teacherItem" :title="item[4]">

                        <div v-if="item[3].slice(-1) == '/'">
                            <img :src=nonePhoto width="114" height='156' style="border-radius:10px;box-shadow:2px 2px 5px #333333;"></img>
                        </div>
                        <div v-else>
                            <img :src=item[3] width="114" height='156' style="border-radius:10px;box-shadow:2px 2px 5px #333333;"></img>
                        </div>

                        <p style="color:black;font-weight:bold;font-size:20px;">{{item[0]}}</p></a>

                        <p style="font-size:15px;">{{item[1]}}</p>
                        <p style="font-size:15px;">{{item[2]}}</p>
                    </div>

                </div>
            </div>
    </div>

    </div>
</template>

<script>
import teacher from '../../static/学术团队/教师/teacher'
import graduate from '../../static/学术团队/研究生/graduate'
import bachelor from '../../static/学术团队/本科生/bachelor'
  export default {
    name:'academicTeam',
    data(){
        return {
            constTeacherPath:'../../static/学术团队/教师/',
            teacherInfo:teacher.teacherInfo,
            constGraduatePath:'../../static/学术团队/研究生/',
            graduateInfo:graduate.graduateInfo,
            constBachelorPath:'../../static/学术团队/本科生/',
            bachelorInfo:bachelor.bachelorInfo,
            nonePhoto:'../../static/学术团队/config_file/nonePhoto.jpg',

        }
    },
    computed:{
        newTeacherInfo:function() {
            let temp=[];
            let newPath=[];
            for(let i=0;i < this.teacherInfo.length;i++){
                temp=[];
                for(let j=0;j < this.teacherInfo[0].length;j++){
                    if(j != 3){
                        temp.push(this.teacherInfo[i][j]);
            
                    }else{
                        temp.push(this.constTeacherPath + this.teacherInfo[i][j]);
                    }
                }
                newPath.push(temp);
            }
            return newPath;
        },
        newGraduateInfo:function() {
            let temp=[];
            let newPath=[];
            for(let i=0;i < this.graduateInfo.length;i++){
                temp=[];
                for(let j=0;j < this.graduateInfo[0].length;j++){
                    if(j != 3){
                        temp.push(this.graduateInfo[i][j]);
            
                    }else{
                        temp.push(this.constGraduatePath + this.graduateInfo[i][j]);
                    }
                }
                newPath.push(temp);
            }
            return newPath;
        },
        newBachelorInfo:function(){
            let temp=[];
            let newPath=[];
            for(let i=0;i < this.bachelorInfo.length;i++){
                temp=[];
                for(let j=0;j < this.bachelorInfo[0].length;j++){
                    if(j != 3){
                        temp.push(this.bachelorInfo[i][j]);
            
                    }else{
                        temp.push(this.constBachelorPath + this.bachelorInfo[i][j]);
                    }
                }
                newPath.push(temp);
            }
            return newPath;
        }
    },
  }
</script>


<style>
  *{
      margin:0px;
      padding:0px;
  }
  #academicTeam{

  }
  #teacher{
    display:grid;
    grid-template-columns: repeat(5,20%);
  }
</style>



