<template>
    <div id="researchProject" style="margin-top:20px;border:5px solid gray;width:80vw;margin-left:10vw;margin-bottom:60px">
        <img src="../../static/科研项目/show.jpg" style="width:80vw;height:30vh;border:1px solid gray"></img>
        <div v-if="zongxiang !=''">
            <h1 style="margin:15px;" align="">纵向项目</h1>
            <div v-for="item,inx in zongxiang"  align="left" style="font-size:19px;margin-left:15px;">
             <span><i class="el-icon-circle-check" style="color:#04BFEA"></i> {{inx+1}}. {{item}}</span>
            </div>
        </div>


        <div style="margin:20px"><hr/></div>
        
        <div v-if="hengxiang !=''" style="margin-bottom:15px">
            <h1 style="margin:15px;margin-top:40px;">横向项目</h1>
            <div v-for="item,inx in hengxiang"  align="left" style="font-size:19px;margin-left:15px">
             <span ><i class="el-icon-circle-check" style="color:#04BFEA"></i> {{inx+1}}. {{item}}</span>
            </div>
        </div>
        
  </div>
</template>

<script>
import researchProject from '../../static/科研项目/researchProject'
  export default {
    name:'researchProject',
    data(){
        return {
            zongxiang:researchProject.projectInfo['zongxiang'],
            hengxiang:researchProject.projectInfo['hengxiang'],
        }
    }
  }
</script>


<style>
  *{
      margin:0px;
      padding:0px;
  }
  #researchProject{
    
  }
</style>


