<template>
    <div id="theRoadofSDN" style="margin-top:20px;border:5px solid gray;width:80vw;margin-left:10vw;">
        <img src="../../static/SDN之路/show.jpg" style="width:80vw;height:30vh;border:1px solid gray;"></img>
        <div style="margin-bottom:50px;"></div>
        <div class="block">
        <el-timeline v-for="item,inx in roadInfo" :key="inx">
            <el-timeline-item>
            <el-card>
                <a :href=item[1] style="text-decoration:none;color:#04BFEA;font-size:20px">{{item[0]}}</a>
            </el-card>
            </el-timeline-item>
        </el-timeline>
        </div>



  </div>
</template>

<script>

import theRoadofSDN from  '../../static/SDN之路/theRoadofSDN'
  export default {
    name:'theRoadofSDN',
    data(){
        return {
            roadInfo:theRoadofSDN.roadInfo,
        }
    }
  }
</script>


<style>
  *{
      margin:0px;
      padding:0px;
  }
  .block{
    width:400px;
    
    margin-bottom:50px;
    margin-left:40vw;
    transform:translateX(-50%);
  }
</style>


