<template>
    <div id="dynamic" style="margin-top:20px;border:5px solid gray;width:80vw;margin-left:10vw;margin-bottom:60px">
        <img src="../../static/学术动态/show.jpg" style="width:80vw;height:30vh;border:1px solid gray;"></img>

        <div v-if="contentPath !== ''">
            <iframe :src=contentPath style="width:100%;height:60vh"></iframe>
        </div>


        <div v-for="item,inx in dynamicInfo" style="margin-left:5vw;margin-top:20px;">
            <div style="font-size:22px;margin-left:1vw;color:#f15a23" align="left" >{{item.year}}</div>
            <div v-for="i,inx in item['content']">
                <div align="left" style="margin:20px;margin-left:3vw;font-size:19px;font-family:'微软雅黑'">
                    <i class="el-icon-circle-check" style="color:#04BFEA"></i> {{inx+1}}.
                    <span @click="func" v-html='i' class="linkWords"></span>
                </div>
                
            </div>
        </div>
  </div>
</template>

<script>

import dynamic from  '../../static/学术动态/dynamic'
  export default {
    name:'dynamic',
    data(){
        return {
            dynamicInfo:dynamic.dynamicInfo,
            contentPathConst:'../../static/学术动态/动态内容/',
            contentPath:'',
            iframePage:'',
        }
    },
    methods:{
        func(e){
            this.contentPath = e.srcElement.href;
            this.contentPath = this.contentPathConst + this.contentPath.split('/').slice(-1)[0];
            e.preventDefault();
            e.stopPropagation();
        },
        test(i){
            // console.log(i);
        }
    }
  }
</script>


<style>
  *{
      margin:0px;
      padding:0px;
  }
  .block{
    width:400px;
    
    margin-bottom:50px;
    margin-left:40vw;
    transform:translateX(-50%);
  }
  .linkWords{
      
  }
</style>


